import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';

import { useRenderProps } from '@vl/hooks/useRenderProps';

import AdvisorItemX from '@uz/unitz-components-web/AdvisorItemX';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Button } from '@uz/unitz-components-web/Button';
import gstyles from '@vl/gstyles';
import { ResponsiveProps } from '../Responsive';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <DIV forceCtx>
        {ctx.debug(() => {
          ctx.set('@item', item);
          ctx.set('@index', index);
        })}
        <div>
          <AdvisorItemX />
        </div>
      </DIV>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="component advisorListY 779BD3C8-CDF6-4067-A5A5-C1B320DFF758">
    <div className="mb-3">
      <FlatList
        data={ctx.get('paginationModel.items', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
        horizontal
      />
    </div>
    <div className="hidden lg:block border-t border-divider" />
    <div className="py-6 flex space-x-4 justify-center lg:justify-end items-center lg:items-end">
      <div className="text-right">
        <ResponsiveProps
          xs={{
            rerender: () => (
              <Button
                className="w-10 sm:w-32"
                onClick={ctx.get('paginationModel.onPreviousPage')}
                name="ink"
                disabled={!ctx.get('paginationModel.hasPreviousPage')}
                icon={gstyles.icons({
                  name: 'arrow-left',
                  size: 20,
                  fill: gstyles.colors.brownGrey,
                  className: 'inline-block',
                })}
              ></Button>
            ),
          }}
          lg={{
            rerender: () => (
              <Button
                className="w-10 sm:w-32"
                onClick={ctx.get('paginationModel.onPreviousPage')}
                name="ink"
                disabled={!ctx.get('paginationModel.hasPreviousPage')}
                icon={gstyles.icons({
                  name: 'arrow-left',
                  size: 20,
                  fill: gstyles.colors.grayScale300,
                  className: 'inline-block mr-2',
                })}
              >
                {ctx.apply('i18n.t', 'Selection.prevPage')}
              </Button>
            ),
          }}
        >
          {(resProps) => !!resProps && resProps.rerender()}
        </ResponsiveProps>
      </div>
      <div className="self-center text-center">
        <span className="text-base font-normal text-sub">
          {`${ctx.apply('i18n.t', 'ProfileAdvisor.page')} 
          ${ctx.get('paginationModel.page') + 1}`}
        </span>
      </div>
      <div>
        <ResponsiveProps
          xs={{
            rerender: () => (
              <Button
                className="w-10 sm:w-32"
                onClick={ctx.get('paginationModel.onNextPage')}
                name="ink"
                disabled={!ctx.get('paginationModel.hasNextPage')}
                icon={gstyles.icons({
                  name: 'arrow-right',
                  size: 20,
                  fill: gstyles.colors.grayScale300,
                  className: 'inline-block',
                })}
              ></Button>
            ),
          }}
          lg={{
            rerender: () => (
              <Button
                className="w-10 sm:w-32"
                onClick={ctx.get('paginationModel.onNextPage')}
                reverse="true"
                name="ink"
                disabled={!ctx.get('paginationModel.hasNextPage')}
                icon={gstyles.icons({
                  name: 'arrow-right',
                  size: 20,
                  fill: gstyles.colors.grayScale300,
                  className: 'inline-block ml-2',
                })}
              >
                {ctx.apply('i18n.t', 'Selection.nextPage')}
              </Button>
            ),
          }}
        >
          {(resProps) => !!resProps && resProps.rerender()}
        </ResponsiveProps>
      </div>
    </div>
  </DIV>
);

export default View8;
