import React from 'react';
import { bindings, hook } from '@vl/redata';
import useLoadingModel from '@vl/hooks/useLoadingModel';
import _ from 'lodash';
import PaginationModel from '@uz/unitz-models/PaginationModel';
import AdvisorModel from '@uz/unitz-models/AdvisorModel';

import { useFormik } from 'formik';
import * as Yup from 'yup';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            loadingModel: hook(useLoadingModel(() => ({ fetchProfile: true }))),

            featuredCategories: hook(() => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const featuredCategories = _.get(routeParams, 'children', []);
              return featuredCategories;
            }),

            ctfCategories: hook((ctx) => {
              const categories = ctx.apply('ctf.buildCategoryHierarchy');
              const optimizeFilter = React.useMemo(
                () => _.filter(categories, (item) => {
                  return !_.get(item, 'parent');
                }),
                [categories]
              );
              return optimizeFilter;
            }),

            priceRangeOptions: hook(() => {
              const options = {
                min: 100000,
                max: 5000000,
              };
              options.step = (options.max - options.min) / 20;
              options.defaultValue = [options.min + options.step, options.max - options.step];
              return options;
            }),

            advisorFilterForm: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const category = _.get(routeParams, 'id', '');
              const service = _.compact(_.castArray(_.get(routeParams, 'service', '')));
              const form = useFormik({
                initialValues: {
                  category,
                  priceRange: [
                    _.get(routeParams, 'min', ctx.get('priceRangeOptions.min', 0)),
                    _.get(routeParams, 'max', ctx.get('priceRangeOptions.max', 0)),
                  ],
                  rating: '',
                  service,
                },
                onSubmit: async (values, actions) => {
                  try {
                    const advisorFilterPagingModel = ctx.apply('REF.getRef', 'advisorFilterPagingModel');
                    if (advisorFilterPagingModel) {
                      await advisorFilterPagingModel.onLoadPage(0);
                    }
                    const { priceRange } = form.values;
                    const [minPrice, maxPrice] = priceRange;
                    // local routing to update querystring
                    route.navigate(
                      route.to(route.location.pathname, {
                        ..._.omit(form.values, ['priceRange']),
                        min: minPrice,
                        max: maxPrice,
                      })
                    );
                  } catch (error) {
                    actions.setStatus({
                      error,
                    });
                  }
                },
                validationSchema: Yup.object().shape({}),
              });

              return form;
            }),

            getFilterArgs: hook((ctx) => {
              return () => {
                const advisorFilterFormValues = ctx.get('advisorFilterForm.values', {});
                const min_service_price = _.get(advisorFilterFormValues, 'priceRange.0', '');
                const max_service_price = _.get(advisorFilterFormValues, 'priceRange.1', '');
                const min_review_score = _.get(advisorFilterFormValues, 'rating', '');
                const args = {
                  category: _.castArray(_.get(advisorFilterFormValues, 'category', '')).join(','),
                  service: _.castArray(_.get(advisorFilterFormValues, 'service', '')).join(','),
                  ...(min_service_price ? { min_service_price: _.parseInt(min_service_price) } : {}),
                  ...(max_service_price ? { max_service_price: _.parseInt(max_service_price) } : {}),
                  ...(min_review_score ? { min_review_score: _.parseInt(min_review_score) } : {}),
                };
                return args;
              };
            }),

            advisorFilterPagingModel: hook((ctx) => {
              const ref = React.useRef({});
              _.assign(ref.current, { getFilterArgs: ctx.get('getFilterArgs') });
              const pagingModel = React.useMemo(() => {
                // Create an instance with a loader function is the dataLoader variable.
                const pagingModel = PaginationModel.fromConfig({
                  Model: AdvisorModel,
                  dataQuery: `
                    search_advisor(args: $args, where: {is_active: {_eq: true}}, order_by: {review_agg: {avg_score: desc}}) {
                      id
                      profile {
                        id
                        display_name
                        avatar_url
                        ref_ctf_eid
                        advisor_id
                        created_at
                        intro_text
                        detail_html
                        years_experience
                        slug
                        advisor_number
                        appraisal_type
                        advisor {
                          transactions_aggregate {
                            aggregate {
                              count
                            }
                          }
                        }
                      }
                      services {
                        price {
                          per_unit
                          per_amount
                          price_currency
                          price_amount
                          id
                          service {
                            id
                          }
                        }
                      }
                      services_aggregate {
                        aggregate {
                          count
                        }
                      }
                      review_agg {
                        avg_score
                      }
                      presence{
                        status
                      }
                      readiness{
                        is_ready
                        on_transaction_id
                        is_accept_service
                      }
                    }
                  `,
                  aggQuery: `
                    search_advisor_aggregate(args: $args) {
                      aggregate {
                        count
                      }
                    }
                  `,
                  pageSize: 12,
                  varsList: '$args: search_advisor_args!',
                  getVars: () => {
                    return {
                      args: ref.current.getFilterArgs(),
                    };
                  },
                });
                return pagingModel;
              }, [ref]);

              pagingModel.hookState();

              React.useEffect(() => {
                pagingModel.onLoadPageHandler(0);
              }, [pagingModel]);

              const pagination = {
                items: pagingModel.useState('items'),
                page: pagingModel.useState('page'),
                total: pagingModel.useState('total'),
                isLoading: pagingModel.useState('isLoading'),
                hasPreviousPage: pagingModel.useState('hasPreviousPage'),
                hasNextPage: pagingModel.useState('hasNextPage'),
                onRefresh: (...args) => pagingModel.onRefresh(...args),
                onPreviousPage: (...args) => pagingModel.onPreviousPageHandler(...args),
                onNextPage: (...args) => pagingModel.onNextPageHandler(...args),
                onLoadPage: (...args) => pagingModel.onLoadPageHandler(...args),
              };
              ctx.apply('REF.setRef', 'advisorFilterPagingModel', pagination);
              return pagination;
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
