import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';
import CFCategoryRenderer from '@uz/unitz-models/CFCategoryModel/renderer';

const bindData = bindings({
  banner: {
    rules: [
      [
        'data',
        {
          data: {
            categoryInfomation: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const image = _.get(routeParams, [..._.toPath('image'), 'fixed'], []);
              return {
                categoryName: _.get(routeParams, 'displayName', ''),
                description: CFCategoryRenderer.longText(ctx)(routeParams),
                image: `https:${_.get(image, 'src', '//via.placeholder.com/1600x280')}`,
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
