const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <Col lg={12} md={12} sm={24} className="mb-4 sm:mb-0 lg:px-0">
        <DIV forceCtx>
          {ctx.debug(() => {
            const categoryData = ctx.apply('ctf.findNode', { id: _.get(item, 'id', '') });
            ctx.set('@item', categoryData);
            ctx.set('@index', index);
          })}
          <CategoryItemX />
        </DIV>
      </Col>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => {
  return (
    <DIV className="FeaturedCategory">
      {!!ctx.get('@items.length') && (
        <div className="py-6 lg:py-16">
          <div className="text-main text-left px-0 font-semibold text-xl lg:text-4xl mb-3">
            {ctx.apply('i18n.t', 'CategoryDetail.recommendedCategories')}
          </div>
          <FlatList
            as={Row}
            gutter={24}
            data={ctx.get('@items', [])}
            renderItem={renderItem}
            keyExtractor={(item) => `${_.get(item, 'id')}`}
          />
        </div>
      )}
    </DIV>
  );
};

export default View8;
